.navbar{
    z-index: 1;
    height: 50px;
    display: flex;
    align-items: center;
    color: #555;
    border: 0.5px solid rgb(231,228,228);
    
    .wrapper{
        width: 100%;
        padding: 10px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .search{
            display: flex;
            align-items: center;
            border: 0.5px solid lightgray;
            padding: 3px;
            input{
                border: none;
                background: transparent;
                outline: none;

                &::placeholder{
                    font-size: 12px;
                }

            }
        }

        .items{
            display: flex;
            align-items: center;
            .item{
                display: flex;
                align-items: center;
                margin-right: 20px;
                position: relative;
                .icon{
                    font-size: 20px;
                }
                .avatar{
                    width: 30px;
                    height: 30px;
                    object-fit: cover;
                    border-radius: 50%;
                }
                .counter{
                    width: 15px;
                    height: 15px;
                    border-radius: 50%;
                    color: white;
                    background-color: red;
                    display: flex;
                    align-items: center;
                    font-size: 10px;
                    font-weight: bold;
                    position: absolute;
                    top: -5px;
                    right: -5px;
                    justify-content: center;
                }
            }
            .responseitem{
                display: none;
                @media only screen and (max-width : 768px){
                    display: inline;
                }
            }
        }

    }
}


.navbar-responsive{
    width: 250px;
    display: none;
    z-index: 2;
    background-color: aqua;
    min-height: 100vh;
    position: fixed;
    right: 0%;

}