.home{
    display: flex;
    .homeContainer{
        flex: 6;

        .widgets ,  .charts{
            display: flex;
            padding: 20px;
            gap: 20px;

            @media screen and (max-width:768px) {
                flex-direction: column;
            }
        }

        .charts{
            padding: 5px 20px;
        }

        .listContainer{
            box-shadow: 2px 4px 10px 1px rgba(0,0,0,0.47);
            padding: 20px;
            margin: 20px;
            .listTitle{
                font-weight: 500;
                color: gray;
                margin-bottom: 15px;
            }
        }
    }
}